export default class Review {
  constructor({
    authorLastName,
    createdDate,
    authorFirstName,
    rating,
    id,
    text,
    title
  }) {
    this._authorLastName = authorLastName
    this._createdDate = createdDate
    this._rating = rating
    this._id = id
    this._text = text
    this._title = title
    this._authorFirstName = authorFirstName
  }
  isValid() {
    return !!(
      this._authorFirstName &&
      this._authorLastName &&
      this._rating &&
      this._id &&
      this._text &&
      this._title
    )
  }
  get name() {
    return `${this._authorFirstName} ${this._authorLastName}`
  }
  get rating() {
    return Math.round(this._rating)
  }
  id() {
    return this._id
  }
  get review() {
    return {
      title: this._title,
      text: this._text
    }
  }
}
